<template>
  <div class="container">
    <van-nav-bar fixed title="黑马头条" right-text="搜索" v-if="showNavBar"  @click-right="$router.push('/search')"></van-nav-bar>
    <div class="my-wrapper" :class="{noTop:$route.path==='/user'}">
    <!-- 二级路由容器 想要缓存路由组件的话，可以直接缓存二级路由容器 -->
    <!-- 如果缓存了这个容器表示这个容器下的所有组件都会被缓存 -->
    <!-- 用keep-alive用来缓存router-view -->
      <keep-alive>
        <router-view></router-view>
      </keep-alive>
    </div>
    <van-tabbar route fixed>
      <van-tabbar-item icon="home-o" to="/">首页</van-tabbar-item>
      <van-tabbar-item icon="chat-o" to="/question">问答</van-tabbar-item>
      <van-tabbar-item icon="video-o" to="/video">视频</van-tabbar-item>
      <van-tabbar-item icon="user-o" to="/user">我的</van-tabbar-item>
    </van-tabbar>
  </div>
</template>

<script>
export default {
  computed: {
    showNavBar () {
      return this.$route.path !== '/user'
    }

  }
}
</script>

<style lang="less" scoped>
.container{
  width: 100%;
  height: 100%;
  position: relative;
  .my-wrapper{
    width: 100%;
    height: 100%;
    overflow: hidden;
    padding-top: 46px;
    padding-bottom: 50px;
    box-sizing: border-box;
    &.noTop{
      padding-top: 0;
    }
  }
}
</style>
